.contacts__title-block {
  padding: 32px 0 35px 0;
}

.contacts__title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  color: #000000;
  margin: 0;
}

.contacts__form-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.contacts__label {
  min-width: 50px;
}

.contacts__input-wrapper {
  margin-bottom: 14px;
}
